	{
		var attributes = (typeof(attributesAssoc) == 'undefined') ? {'BSTA' : 'bsta', 'BWL' : 'bwl', 'BBL' : 'bbl'} : attributesAssoc,
			key;
		try 
		{
			for (key in attributes)
				this[key] = parseFloat(node.getAttribute(attributes[key]));
		}
		catch (exception)
		{
		}
		return this;
	}

